.modalBody {
    padding: 35px;
    text-align: center;
}
  
.modalBodyButtons {
    padding-top: 30px;
}

.cancelButton {
    height: 36px;
    font-size: 12px;
    line-height: 12px;
    font-weight: 100;
    padding: 11px 24px;
    background-color: #f6f6f6;
    color: #5c636a;
    border: none;
    border-radius: 4px;
    width: 100%;
    margin-top: 20px;
    margin-left: 0;
    background: none;
}
  
.saveModalButton {
    height: 36px;
    font-size: 12px;
    line-height: 12px;
    font-weight: 100;
    padding: 11px 24px;
    background-color: #008662;
    color: #ffffff;
    border: none;
    border-radius: 4px;
    width: 100%;
    margin-top: 20px;
}