.titleGroupSelect {
  display: flex;
  margin-right: 15px;
}

.titleGroupSelect label {
  font-weight: 400;
  margin-top: 3px;
  margin-right: 5px;
}

.actionButtons img {
  cursor: pointer;
  margin-top: 5px;
}

.disabledActionButton {
  opacity: 0.6;
  cursor: not-allowed !important;
}

.actionButtons img + img {
  margin-left: 15px;
}

/* Available Program */

.headerSearch {
  padding: 3px 10px;
  height: 38px;
  background: #ffffff;
  color: #6d6d73;
  border: 1px solid #ced4da;
  border-right: none;
  box-sizing: border-box;
  border-radius: 4px 0px 0px 4px;
  font-size: 12px;
  line-height: 14px;
  outline: none;
  box-shadow: none;
  margin-bottom: 15px;
}

.headerSearch::placeholder {
  color: #b1b1b1;
}

.headerSearch:disabled {
  background: #e9ecef;
}

.divButton {
  justify-content: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-left: 40px;
  margin-right: 40px;
}

.addButton {
  background-color: #008662;
  border-radius: 4px;
  border: none;
  color: #fff;
  font-weight: 200;
  font-size: 12px;
  padding: 5px;
  width: 90px;
  display: flex;
}

.addButton:disabled {
  background-color: #b1b1b1;
  cursor: not-allowed;
}

.addButton:after {
  width: 30px;
  margin-top: 10px;
  content: url('../../assets/icons/arrow-left.svg');
}

.removeButton {
  margin-top: 10px;
  background-color: #008662;
  border-radius: 4px;
  border: none;
  color: #fff;
  font-weight: 200;
  font-size: 12px;
  padding: 5px;
  width: 90px;
  display: flex;
}

.removeButton:before {
  width: 30px;
  margin-top: 10px;
  content: url('../../assets/icons/arrow-right.svg');
}

.removeButton:disabled {
  background-color: #b1b1b1;
  cursor: not-allowed;
}

.divNoAvailableChannelSelected {
  height: calc(100vh - 253px);
  border-radius: 6px;
  color: #008662;
  font-weight: 300;
  font-size: 24px;
  text-align: center;
  background-color: #e8e8e8;
  padding-left: 25px;
  padding-right: 25px;
  display: table;
  width: 100%;
  font-size: 28px;
  margin-top: 23px;
}

.divNoAvailableChannelSelectedSmall {
  height: calc(100vh - 456px);
  border-radius: 6px;
  color: #008662;
  font-weight: 300;
  font-size: 24px;
  text-align: center;
  background-color: #e8e8e8;
  padding-left: 25px;
  padding-right: 25px;
  display: table;
  width: 100%;
  font-size: 28px;
  margin-top: 25px;
}

.chevLastUp,
.chevUp,
.chevLastDown,
.chevDown {
  background: #008662;
  border-radius: 4px;
  width: 26px;
  height: 26px;
  display: block;
  margin-left: 5px;
  border: none;
  margin-bottom: 5px;
  position: relative;
}

.chevLastUp:focus,
.chevUp:focus,
.chevLastDown:focus,
.chevDown:focus {
  outline: none;
}

.chevLastUp:disabled,
.chevUp:disabled,
.chevLastDown:disabled,
.chevDown:disabled {
  background-color: #b1b1b1;
  cursor: not-allowed;
}

.chevLastUp {
  margin-top: 69px;
}

.chevLastUp::before {
  content: url('../../assets/icons/ChevLast-Up-White.svg');
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.chevUp:before {
  content: url('../../assets/icons/Chev-Up-White.svg');
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.chevDown:before {
  content: url('../../assets/icons/ChevLast-Down-White.svg');
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.chevLastDown:before {
  content: url('../../assets/icons/Chev-Down-White.svg');
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.divButtons {
  min-width: 102px;
  max-width: 102px;
  margin-left: 6px;
  margin-right: 6px;
  justify-content: center;
  display: flex;
  flex-direction: column;
}

.divButtons button {
  display: flex;
  height: 38px;
  padding: 1px 5px 0px 5px;
  background-color: #008662;
  font-size: 12px;
  font-weight: 100;
  border-radius: 4px;
  border: 1px solid #008662;
  color: #ffffff;
}

.divButtons button + button {
  margin-top: 5px;
}

.divButtons button:active,
.divButtons button:hover {
  background-color: #116c53;
  border: 1px solid #116c53;
}

.divButtons button:focus {
  border: 1px solid #116c53;
  outline: none;
}

.divButtons button:disabled {
  background-color: #b1b1b1;
  border: 1px solid #b1b1b1;
  cursor: not-allowed;
}

.searchWrapper {
  display: flex;
  width: 100%;
  margin-bottom: 5px;
}

.search {
  height: 28px;
  width: 100%;
  border: 1px solid #b1b1b1;
  border-right: none;
  border-radius: 4px 0px 0px 4px;
  font-size: 12px;
  text-indent: 5px;
}

.search:focus {
  outline: none;
}

.timesIcon {
  content: url('../../assets/icons/times.svg');
  width: 12.7px;
  height: auto;
  margin-top: 6px;
  margin-left: 10px;
  cursor: pointer;
}

.searchIcon {
  content: url('../../assets/icons/magnifying-glass.svg');
  margin-top: 6px;
  margin-left: 5px;
  cursor: pointer;
}

.timesIcon:hover,
.searchIcon:hover {
  filter: brightness(0) saturate(100%) invert(30%) sepia(86%) saturate(1538%)
    hue-rotate(142deg) brightness(87%) contrast(101%);
}

.timesIconWrapper {
  width: 30px;
  background-color: #ffffff;
  border: 1px solid #b1b1b1;
  border-left: none;
  border-right: none;
}

.searchIconWrapper {
  width: 30px;
  background-color: #ffffff;
  border: 1px solid #b1b1b1;
  border-left: none;
  border-radius: 0px 4px 4px 0px;
}

.tableLabel {
  color: #6d6d73;
  font-size: 14px;
  font-weight: 400;
  margin-bottom: 3px;
}

.buttons {
  width: 100%;
  text-align: end;
  margin-top: 8px;
}

.programDetailsWrapper {
  width: 100%;
  margin-top: 8px;
}

.verbiage {
  display: table-cell;
  vertical-align: middle;
}

.selectedTableWrapper {
  width: 31%;
  min-height: 31%;
  max-height: 31%;
}

.removeAllButton {
  padding-top: 9px !important;
}

.arrowIcon {
  margin-top: 11px;
}

.removeAllButton img {
  margin-top: 3px;
}

.removeAllButton span {
  margin-left: 5px;
}

.programmeTitlesTableWrapper {
  width: 60%;
  min-height: 60%;
  max-height: 60%;
}

.divider {
  background: rgb(200, 200, 200);
}

.addIcon:hover{
  cursor: pointer;
}

.addIcon{
height: 23px;
transform: rotate(-45deg);
width: auto;
margin-top: 6px;
}