.tableBodyDiv {
  overflow-x: auto;
  overflow-y: auto;
  height: calc(100vh - 373px);
}

.tableBodyDivSmall {
  overflow-x: auto;
  overflow-y: auto;
  height: calc(100vh - 575px);
}

.tableBodyDiv::-webkit-scrollbar-thumb,
.tableBodyDivSmall::-webkit-scrollbar-thumb {
  background-color: #cbcbcb;
  box-shadow: inset 0 0 0 3px #ffffff;
  border-left: 1px solid #dee3eb;
  border-right: 1px solid #dee3eb;
}

.tableBodyDiv::-webkit-scrollbar-track,
.tableBodyDivSmall::-webkit-scrollbar-track {
  border-right: 1px solid #dee3eb;
  border-left: 1px solid #dee3eb;
}

.tableBodyDiv::-webkit-scrollbar,
.tableBodyDivSmall::-webkit-scrollbar {
  background-color: #ffffff;
  width: 14px;
  padding: 5px;
}

.isHighlighted {
  border-right: 2px solid #008662;
}