.panel {
  background: #ffffff;
  box-shadow: 0px 15px 50px rgba(0, 0, 0, 0.15);
  border-radius: 5px;
  padding: 15px 17px;
  width: 100%;
  border: 1px solid #008662;
  height: 198px;
}

.content {
  height: 150px;
  overflow-x: hidden;
  overflow-y: auto;
}

.test 
{
  display: block;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-size: 16px;
}
.content::-webkit-scrollbar-thumb {
  background-color: #D2D2D2;
}

.content::-webkit-scrollbar {
  background-color: #D2D2D233;
  width: 14px;
  padding: 5px;
}

.labelField {
  color: #b1b1b1;
  width: auto;
  font-weight: 400;
  font-size: 12px;
  font-weight: normal;
  display: block;
}

.closeIcon {
  right: 0;
  width: 12.73px;
  height: 12.73px;
  position: absolute;
  filter: brightness(0) invert(0.7);
  cursor: pointer;
}

.fontBold {
  list-style-type: none;  
  color: #6D6D73;
  font-weight: bolder;
  font-weight: 600;
  font-size: 12px;
  display: block;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.titleAttributesOverrideIcon {
  margin-left: 10px;
  margin-top: -5px;
  cursor: pointer;
}

.attributesOverrideIcon {
  height: 15px;
  margin-left: 5px;
  margin-top: -3px;
  cursor: pointer;
}